import {
  Button,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
} from '@mui/material';
import { useRouter } from 'next/router';
import React, { useEffect, useState } from 'react';
import { useAuthAPI } from '../../hooks/authAPI';

export default function AdminUserTable({ roleNameProp = '' }) {
  const router = useRouter();
  const [userList, setUserList] = useState([]);
  const authAPI = useAuthAPI();

  const handleRouteToUserPage = (userName = '') => {
    if (userName) {
      router.push('/admin/user/' + userName);
    }
  };

  useEffect(async () => {
    let data = [];
    if (roleNameProp) {
      data = await authAPI.getRoleUsers(roleNameProp);
    } else {
      data = await authAPI.getUsers();
    }
    if (data) {
      setUserList(data);
    }
  }, []);

  return (
    <>
      <strong style={{ float: 'left', paddingBottom: '1em' }}>Users</strong>
      <TableContainer component={Paper}>
        <Table sx={{ minWidth: 300 }} aria-label='Admin User Table'>
          <TableHead>
            <TableRow>
              <TableCell>Name</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {userList?.map((currentUser, index) => {
              return (
                <TableRow key={index} hover>
                  <TableCell>
                    <Button onClick={() => handleRouteToUserPage(currentUser)}>
                      {currentUser}
                    </Button>
                  </TableCell>
                </TableRow>
              );
            })}
          </TableBody>
        </Table>
      </TableContainer>
    </>
  );
}
